.fade-in {
  -webkit-animation: fadein 3s; /* Safari, Chrome and Opera > 12.1 */
     -moz-animation: fadein 3s; /* Firefox < 16 */
      -ms-animation: fadein 3s; /* Internet Explorer */
       -o-animation: fadein 3s; /* Opera < 12.1 */
          animation: fadein 3s;
}

@keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}

/* Internet Explorer */
@-ms-keyframes fadein {
  from { opacity: 0; }
  to   { opacity: 1; }
}
.home-container {
  display: flex;
  flex-direction: column;
  width: 100vw;
}
.nav-bar {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  height: 90px;
  position: fixed;
  background-color: #222222;
  z-index: 5;
}
.nav-bar-item {
  cursor: default;
  color: #8eb849;
  font-size: 16px;
  font-weight: bold;
  margin-right: 20px;
  margin-left: 20px;
  padding: 10px;
  transition: color 0.5s;
  text-align: center;
  transition: color 0.5s ;
}
.nav-bar-item:hover {
  color: tomato;
}
.active-item {
  color: white;
}
.home-page-section {
  display: flex;
  width: 100%;
  height: 600px;
  margin-top: 110px;
  overflow: hidden;
}
.home-page-welcome {
  cursor: default;
  margin-top: 70px;
  margin-left: 20%;
  transition: all 0.5s;
}
.home-page-fname{
  color: white;
  font-size: 90px;
  font-weight: bold;
  transition: all 0.5s;
}
.home-page-lname{
  color: white;
  font-size: 90px;
  font-weight: bold;
  margin-top: -20px;
  transition: all 0.5s;
}
.home-page-photo-div {
  position: relative;
  margin-left: -5%;
  transition: all 0.5s;
}
.home-page-photo {
  height: 800px;
  filter: brightness(50%);
  transition: all 0.5s;
  transform: scale(1);
}
.home-page-photo:hover {
  filter: brightness(70%);
  transform: scale(1.1);
}
@media screen and (min-width: 801px) {
  .home-page-photo-div {
    margin-left: -5%;
  }
  .home-page-welcome {
    margin-left: 18%;
  }
  .home-page-fname{
    font-size: 90px;
  }
  .home-page-lname{
    font-size: 90px;
  }
}

/* If the screen size is 600px wide or less, set the font-size of <div> to 30px */
@media screen and (max-width: 800px) {
  .home-page-photo-div {
    margin-left: -15%;
  }
  .home-page-welcome {
    margin-left: 5%;
  }
  .home-page-fname {
    font-size: 50px;
  }
  .home-page-lname{
    font-size: 50px;
  }
}
.Achievements-page-section {
  display: flex;
  width: 100%;
  height: 890px;
  overflow: hidden;
  background-color: tomato;
}
.home-page-description {
  cursor: default;
  position: relative;
  color: white;
  font-size: 24px;
  font-weight: bold;
  width: 30%;
  margin-top: 300px;
  margin-right: 50px;
  border-top: 5px solid #8eb849;
  transition: all 0.5s;
}
.home-page-description-small {
  cursor: default;
  color: white;
  font-size: 24px;
  font-weight: bold;
  width: 90%;
  margin-top: 20px;
  border-top: 5px solid #8eb849;
  transition: all 0.5s;
}
.home-page-icons{
  display: flex;
  font-size: 28px;
  color: #8eb849;
}
.home-social-icons {
  cursor: pointer;
  padding-right: 20px;
  transition: all 0.5s;
}
.home-social-icons:hover {
  color: tomato;
}
.fade-in-section {
  opacity: 0;
  transform: translateY(20vh);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 1.2s ease-out;
  will-change: opacity, visibility;
}
.fade-in-section.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}
.download-cv {
  cursor: pointer;
  display: flex;
  padding-left: 5px;
  padding-right: 5px;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: bold;
  width: 100px;
  background-color: #8eb849;
  color: black;
  border-radius: 5px;
  transition: all 0.5s;
}
.download-cv:hover {
  background-color: tomato;
}