/* Internet Explorer */
.Education-page-section {
  cursor: default;
  display: flex;
  width: 100%;
  height: 800px;
  overflow: hidden;
  background-color: #1f1f1f;
  transition: all 0.5s;
}
.education-page-title {
  cursor: default;
  display: flex;
  position: absolute;
  font-size: 20vw;
  color: #222222;
  margin-top: -10%;
  z-index: 1;
}
.hover-to-tomato {
  color: #222222;
  transition: color 0.5s;
}
.hover-to-tomato:hover {
  color: tomato;
}
.education-page-cards-container {
  align-self: center;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 500px;
  width: 100%;
  z-index: 2;
  transition: all 0.5s;
}
.educatio-page-card{
  display: flex;
  text-align: center;
  flex-direction: column;
  align-items: center;
  margin: 40px;
  padding: 10px;
  width: 400px;
  height: 500px;
  background: rgba(88, 88, 88, 0.25);
  backdrop-filter: blur( 6.0px );
  -webkit-backdrop-filter: blur( 6.0px );
  border-radius: 10px;
  transition: all 0.5s;
}
.educatio-page-card:hover {
  border-top: 20px solid tomato;
  border-bottom: 20px solid tomato;
}
.education-page-card-title {
  font-size: 20px;
  color: white;
}
.education-page-card-icon{
  font-size: 60px;
  color: #8eb849;
  padding: 20px;
  padding-bottom: 30px;
  margin-top: 30px;
}
.education-page-card-subtitle {
  font-size: 16px;
  color: rgb(211, 211, 211);
  padding: 10px;
} 
.education-page-card-number-title {
  font-size: 20px;
  color: white;
  padding-top: 20px;
}
.education-page-card-number {
  font-size: 80px;
  color: rgb(211, 211, 211);
}
.card{
  position: relative;
  box-shadow:0 2px 10px rgba(0,0,0,.2);
}
.card:before,
.card:after
{
  content:"";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 4px;
  transition: 0.5s;
  z-index:-1;
}
.card:hover:before{
transform: rotate(20deg);
box-shadow: 0 2px 20px rgba(0,0,0,.2);
}
.card:hover:after{
transform: rotate(10deg);
box-shadow: 0 2px 20px rgba(0,0,0,.2);
}

.Research-page-section {
  cursor: default;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background-color: #222222;
}
.research-container {
  display: flex;
  flex-direction: column;
  width: 800px;
  margin-top: 40px;
  margin-right: 80px;
  margin-bottom: 40px;
  padding: 20px;
}
.section-title {
  display: flex;
  font-size: 24px;
  font-weight: bold;
  color: white;
}
.research-card {
  width: 100%;
  margin-top: 20px;
  padding: 20px;
  border-radius: 10px;
  background: linear-gradient(138deg, rgba(79,79,79,1) 0%, rgba(47,47,47,1) 26%, rgba(34,34,34,1) 63%);
  transition: all 0.5s;
}
.research-card:hover {
  box-shadow: 28px 20px 25px -21px rgba(0,0,0,0.75);
}
.research-card-title {
  font-size: 18px;
  color: white;
}
.research-card-description {
  font-size: 14px;
  text-align: left;
  margin-top: 20px;
  color: rgb(211, 211, 211);
}
.gradient-line {
  align-self: center;
  margin-left: 30px;
  width: 100%;
  height: 5px;
  border-radius: 10px;
}
.gradient {
  background: linear-gradient(270deg, #8eb849, #ff6347);
  background-size: 400% 400%;
  -webkit-animation: AnimationName 10s ease infinite;
          animation: AnimationName 10s ease infinite;
}

@-webkit-keyframes AnimationName {
  0%{background-position:0% 50%}
  50%{background-position:100% 50%}
  100%{background-position:0% 50%}
}

@keyframes AnimationName {
  0%{background-position:0% 50%}
  50%{background-position:100% 50%}
  100%{background-position:0% 50%}
}
